*{
  box-sizing: border-box;
}

body{
  background-color: #f7fafc;
}

.App {
  padding: 0 30px;
}

.form{
  border-radius: .5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, .1), 0 4px 6px -2px rgba(0, 0, 0, .05);
  padding: 1rem;
  background-color: #fff;
}

.form h2{
  margin: 0;
}

.form input{
  width: 300px;
}

.form textarea{
  width: 100%;
}

.form input, .form textarea{
  color: #4a5568;
  background-color: #fff;
  border: 1px solid #e2e8f0;
  font-size: 1rem;
  padding: .5rem 1rem;
  margin: 1rem 0;
  display: block;
  border-radius: .5rem;
}

.form button{
  background-color: #4299e1;
  color: #fff;
  font-size: 1rem;
  padding: .5rem;
  border-radius: .5rem;
  border: none;
}

.form button:hover{
  background-color: #2b6cb0
}
